import React from 'react';
import _ from 'lodash';
import ReactHtmlParser from 'react-html-parser';

export default class Footer extends React.Component {
    render() {
        return (
            <footer id="footer">
                <ul className="contact-icons">
                    {_.get(this.props, 'data.site.siteMetadata.footer.social_links') && 
                        _.map(_.get(this.props, 'data.site.siteMetadata.footer.social_links'), (link, link_idx) => (
                            <li key={link_idx} className={'icon ' + _.get(link, 'icon')}><a href={_.get(link, 'link_url')}>{_.get(link, 'text')}</a></li>
                        ))
                    }
                </ul>
                <div className="copyright">
                    {ReactHtmlParser(_.get(this.props, 'data.site.siteMetadata.footer.copyright'))}
                </div>
            </footer>
        );
    }
}
