import _ from 'lodash';

export default function(pages, path) {
    if (_.startsWith(path, '#')) {
        return path;
    } else {
        const pageFields = _.map(pages.edges, edge => edge.node.fields);
        const page = _.find(pageFields, {relativePath: path});
        if (!page) {
            throw new Error('could not find page with path: ' + path);
        }
        return page.url;
    }
}
