import React from 'react';
import _ from 'lodash';
import {withPrefix} from 'gatsby';
import classNames from '../utils/classNames';

import link from '../utils/link';

export default class Menu extends React.Component {
    render() {
        return (
            <nav id="menu">
                <ul className="links">
                    {_.map(_.get(this.props, 'data.menus.main'), (item, item_idx) => (
                        <li key={item_idx}><a href={withPrefix(_.get(item, 'url'))}>{_.get(item, 'title')}</a></li>
                    ))}
                    {_.map(_.get(this.props, 'data.site.siteMetadata.menu.actions'), (action, action_idx) => (
                        <li key={action_idx}><a href={withPrefix(link(this.props.data.pages_urls, _.get(action, 'url')))} className={classNames('button', {'primary': _.get(action, 'is_primary')})}>{_.get(action, 'title')}</a></li>
                    ))}
                </ul>
            </nav>
        );
    }
}
