import React from 'react';
import _ from 'lodash';
import ReactHtmlParser from 'react-html-parser';
import {withPrefix} from 'gatsby';
import classNames from '../utils/classNames';

import markdownify from '../utils/markdownify';
import link from '../utils/link';

export default class Banner extends React.Component {
    render() {
        return (
            <section id={_.get(this.props, 'data.page.frontmatter.banner.section_id')}>
                <div className="inner">
                    <h1>{ReactHtmlParser(_.get(this.props, 'data.page.frontmatter.banner.title').replace(/\n/g, '<br />'))}</h1>
                    {markdownify(_.get(this.props, 'data.page.frontmatter.banner.subtitle'))}
                    {_.get(this.props, 'data.page.frontmatter.banner.actions') && 
                        <ul className="actions special">
                            {_.map(_.get(this.props, 'data.page.frontmatter.banner.actions'), (action, action_idx) => (
                                <li key={action_idx}><a href={(_.get(action, 'url').startsWith('#') ? _.get(action, 'url') : withPrefix(link(this.props.data.pages_urls, _.get(action, 'url'))))} className={classNames('button', 'large', 'wide', {'primary': _.get(action, 'is_primary')}, {'scrolly-middle': _.get(action, 'is_scrolly')})}>{_.get(action, 'label')}</a></li>
                            ))}
                        </ul>
                    }
                </div>
            </section>
        );
    }
}
