import React from 'react';
import _ from 'lodash';
import {withPrefix} from 'gatsby';

import markdownify from '../utils/markdownify';

export default class Posts extends React.Component {
    render() {
        return (
            <section id={_.get(this.props, 'section.section_id')} className="wrapper">
                <div className="inner">
                    <header className="major">
                        <h2>{_.get(this.props, 'section.title')}</h2>
                    </header>
                    <div className="posts">
                        {_.map(_.orderBy(_.get(this.props, 'data.pages_posts.edges', []).map(edge => edge.node), 'frontmatter.date', 'desc'), (post, post_idx) => (
                            <article key={post_idx}>
                                {_.get(post, 'frontmatter.home_image') && 
                                    <a href={withPrefix(_.get(post, 'fields.url'))} className="image"><img src={withPrefix(_.get(post, 'frontmatter.home_image'))} alt="" /></a>
                                }
                                <div className="content">
                                    <h3><a href={withPrefix(_.get(post, 'fields.url'))}>{_.get(post, 'frontmatter.title')}</a></h3>
                                    {markdownify(_.get(post, 'frontmatter.excerpt'))}
                                    <ul className="actions special">
                                        <li><a href={withPrefix(_.get(post, 'fields.url'))} className={'button primary ' + _.get(post, 'frontmatter.home_button_color')}>Learn more</a></li>
                                    </ul>
                                </div>
                            </article>
                        ))}
                    </div>
                </div>
            </section>
        );
    }
}
