import React from 'react';
import _ from 'lodash';
import {withPrefix} from 'gatsby';

import markdownify from '../utils/markdownify';

export default class Carousel extends React.Component {
    render() {
        return (
            <section id={_.get(this.props, 'section.section_id')} className={'wrapper ' + _.get(this.props, 'section.background_color_style')}>
                <div className="inner">
                    <header className="major">
                        <h2>{_.get(this.props, 'section.title')}</h2>
                    </header>
                    <div className="carousel">
                        {_.map(_.orderBy(_.filter(_.get(this.props, 'data.pages_carousel.edges', []).map(edge => edge.node), ['frontmatter.home_sections.carousel.enabled', true]), 'frontmatter.home_sections.carousel.weight'), (page, page_idx) => (
                            _.get(page, 'frontmatter.home_sections.carousel') && 
                                <article key={page_idx}>
                                    <div className="image" data-position={_.get(page, 'frontmatter.home_sections.carousel.data_position')}>
                                        <img src={withPrefix(_.get(page, 'frontmatter.home_sections.carousel.image'))} alt="" />
                                    </div>
                                    <div className="content">
                                        <h3>{_.get(page, 'frontmatter.home_sections.carousel.title')}</h3>
                                        {markdownify(_.get(page, 'frontmatter.home_sections.carousel.text'))}
                                        <ul className="actions special">
                                            <li><a href={withPrefix(_.get(page, 'fields.url'))} className={'button primary ' + _.get(page, 'frontmatter.home_sections.carousel.button_background_color')}>Learn more</a></li>
                                        </ul>
                                    </div>
                                </article>
                        ))}
                        <nav>
                            <a href="#" className="previous"><span className="label">Previous</span></a>
                            <a href="#" className="next"><span className="label">Next</span></a>
                        </nav>
                    </div>
                </div>
            </section>
        );
    }
}
