import React from 'react';
import classNames from '../utils/classNames';
import _ from 'lodash';
import {withPrefix} from 'gatsby';

export default class Header extends React.Component {
    render() {
        return (
            <header id="header" className={classNames({'alt': _.get(this.props, 'data.page.frontmatter.template') === 'home'})}>
                <a className="logo" href={withPrefix(_.get(this.props, 'data.site.pathPrefix'))}>{_.get(this.props, 'data.site.siteMetadata.title')}</a>
                <nav>
                    <ul>
                        <li>
                            <a href="#menu">Menu</a>
                        </li>
                    </ul>
                </nav>
            </header>
        );
    }
}
