import React from 'react';
import _ from 'lodash';
import {withPrefix} from 'gatsby';
import ReactHtmlParser from 'react-html-parser';

import cycler from '../utils/cycler';
import markdownify from '../utils/markdownify';

export default class Spotlight extends React.Component {
    render() {
        let cyc_index = cycler('odd', 'even');
        return (
            _.map(_.orderBy(_.filter(_.get(this.props, 'data.pages_spotlight.edges', []).map(edge => edge.node), ['frontmatter.home_sections.spotlight.enabled', true]), 'frontmatter.home_sections.spotlight.weight'), (page, page_idx) => (
                _.get(page, 'frontmatter.home_sections.spotlight') && 
                    <article key={page_idx} id={_.get(page, 'frontmatter.home_sections.spotlight.section_id')} className={'spotlight ' + cyc_index.next() + ' ' + _.get(page, 'frontmatter.home_sections.spotlight.background_accent')}>
                        <div className="inner">
                            <div className="image" data-position={_.get(page, 'frontmatter.home_sections.spotlight.data_position')}>
                                <img src={withPrefix(_.get(page, 'frontmatter.home_sections.spotlight.image'))} alt="" />
                            </div>
                            <h2>{ReactHtmlParser(_.get(page, 'frontmatter.home_sections.spotlight.title').replace(/\n/g, '<br />'))}</h2>
                            <div className="content">
                                <h3>{ReactHtmlParser(_.get(page, 'frontmatter.home_sections.spotlight.subtitle').replace(/\n/g, '<br />'))}</h3>
                                {markdownify(_.get(page, 'frontmatter.home_sections.spotlight.text'))}
                                <ul className="actions">
                                    <li><a href={withPrefix(_.get(page, 'fields.url'))} className={'button primary ' + _.get(page, 'frontmatter.home_sections.spotlight.button_accent')}>Learn more</a></li>
                                </ul>
                            </div>
                        </div>
                    </article>
            ))
        );
    }
}
