import React from 'react';
import {Helmet} from 'react-helmet';
import _ from 'lodash';

import Header from './Header';
import Menu from './Menu';
import Footer from './Footer';

export default class Body extends React.Component {
    render() {
        return (
            <React.Fragment>
                <Helmet>
                    <title>{_.get(this.props, 'data.page.frontmatter.title') && _.get(this.props, 'data.page.frontmatter.title')  + '-' }{_.get(this.props, 'data.site.siteMetadata.title')}</title>
                    <body className="is-preload" />
                </Helmet>
                    <Header {...this.props} />
                    <Menu {...this.props} />
                    {this.props.children}
                    <Footer {...this.props} />
            </React.Fragment>
        );
    }
}
