import React from 'react';
import _ from 'lodash';
import {withPrefix, graphql} from 'gatsby';
import ReactHtmlParser from 'react-html-parser';

import {Layout} from '../components/index';

export default class Page extends React.Component {
    render() {
        return (
            <Layout {...this.props}>
                <section id="main" className="wrapper">
                    <div className="inner">
                        <header className="major">
                            <h1>{_.get(this.props, 'data.page.frontmatter.title')}</h1>
                        </header>
                        {_.get(this.props, 'data.page.frontmatter.image') && 
                            <span className="image main"><img src={withPrefix(_.get(this.props, 'data.page.frontmatter.image'))} alt="" /></span>
                        }
                        {ReactHtmlParser(_.get(this.props, 'data.page.html'))}
                    </div>
                </section>
            </Layout>
        );
    }
}


export const query = graphql`
    query($url: String!) {
        page: markdownRemark(fields: { url: { eq: $url } }) {
            frontmatter {
                title
                image
                template
            }
            html
        }
        site {
            siteMetadata {
                title
                menu {
                    actions {
                        url
                        is_primary
                        title
                    }
                }
                footer {
                    social_links {
                        icon
                        link_url
                        text
                    }
                    copyright
                }
            }
            pathPrefix
        }
        menus {
            main {
                identifier
                title
                weight
                url
                items {
                    identifier
                    title
                    weight
                    url
                    items {
                        identifier
                        title
                        weight
                        url
                    }
                }
            }
        }
        pages_urls: allMarkdownRemark {
            edges {
                node {
                    fields {
                        relativePath
                        url
                    }
                }
            }
        }
    }
`;
