import React from 'react';
import _ from 'lodash';

import markdownify from '../utils/markdownify';

export default class Cta extends React.Component {
    render() {
        return (
            <section id={_.get(this.props, 'section.section_id')} className={'wrapper ' + _.get(this.props, 'section.style')}>
                <div className="inner">
                    <header className="major">
                        <h2>{_.get(this.props, 'section.title')}</h2>
                        {markdownify(_.get(this.props, 'section.subtitle'))}
                    </header>
                    <form method="post" action="#" className="combined">
                        <input type="email" name="email" id="email" placeholder="Your email address" className="alt accent4" />
                        <input type="submit" className="button primary accent4" value="Subscribe" />
                    </form>
                </div>
            </section>
        );
    }
}
